*{
  box-sizing: border-box;
  padding: 0;;
  margin: 0;
}

body{
  font-family: sans-serif;
}
nav{
  background-color:#71b9c7;
  padding:5px 20px;
}
ul{
  list-style-type: none;
}
a{
  color: white;
  text-decoration: none;
}
.menu1 a:hover{
  text-decoration: underline;
  background-color: #fb7e11;
  font-size: medium;
}
.menu li{
  font-size: 16px;
  padding: 15px 5px;
}

.menu li a{
  display: inline-block;
}

.logo a img{
  width: 110px;
}

/*mobile*/

.menu{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.menu1 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-right: 5%;
}

.menu1 li a {
  padding: 15px;
  background-color: #fca253;
  border-radius: 5px;
  font-size: medium;
}
.hamburg{
  display:none;
}
.Logoname{
margin-left: 5%;
}
h1{
  color: white;
}
/* Footer styling */

footer{
  position: relative;
  z-index: 4;
  color: #c3c7ce;
  padding-top: 128px;
  background-repeat: no-repeat;
  background-position: top center;
}
footer{
  background-image: url('components/Images1/foot-blue.png');
}
.main-footer{

background: #333941;
    padding: 60px 0 50px;
}

 .main-footer ul {
  display: flex;
  flex-direction: row;
  justify-content:space-around;
} 
.social-media {
  padding-top: 20px;
display: block;
justify-content:space-between;
}
.social-media-link{
  display: flex;
  flex-direction: row;
  padding-top: 10px;

}
.social-media a{
  padding: 10px;
  width: 30px;
}
.fotter2{
margin-top:2%;
}
.fotter2 li{
  
  padding-bottom: 20px;
}
.fotter2 li:hover{
  text-decoration: underline;
  padding-bottom: 20px;
}


/*media querry*/

@media only screen and (max-width: 650px) {
  .hamburg {
   display:flex;
   justify-content: flex-end;
   height: 20px;
   margin-top: 2%;
  }
  .menu{
    display: block;
     flex-wrap: wrap;
    text-align: center; 
    justify-content: space-between;
  }
  .menu1 {
    display: none;
  }
  .menumob{
    display: block;
  }
  
  .menu1 li a {
    padding: 10px;
    text-decoration: underline;
  }
  .main-footer ul {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  .social-media-link{
    display: flex;
    flex-direction: row;
   justify-content: center;
  }
}

